.header {
  position: absolute;
  top: 20px;
  width: 100%;
  z-index: 5;

  &__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.social {
  display: flex;
  align-items: center;
  gap: 40px;

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
  }

  &__icon {
    fill: var(--black-color);
    transition: fill .5s ease;
  }
}

.social__link:hover .social__icon {
  fill: var(--accent-color);
}
