:root {
  // base
  --font-family: "Roboto", sans-serif;
  --content-width: 1280px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --second-family: "Archangelsk", sans-serif;

  // colors
  --light-color: #fff;
  --black-color: #000;
  --accent-color: #52bedc
}
