.hero {
  position: relative;
  height: 1000px;
  background-image: url('../img/1-display.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  animation: show 13.5s alternate infinite;


  @keyframes show {
    0% {
      background-image: url('../img/1-display.jpg');
    }
    15% {
      background-image: url('../img/1-display.jpg');
    }
    30% {
      background-image: url('../img/2-display.jpg');
    }
    55% {
      background-image: url('../img/2-display.jpg');
    }
    75% {
      background-image: url('../img/3-display.jpg');
    }
    100% {
      background-image: url('../img/3-display.jpg');
    }
  }

  &__block {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    height: 1000px;
  }

  &__access {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 624px;
    height: 369px;
    background-image: url('../img/background-1.png');
    background-repeat: no-repeat;

    &-title {
      margin: 0;
      margin-bottom: 25px;
      max-width: 484px;
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 62px;
      color: var(--black-color);
    }

    &-btn {
      padding: 19px 50px;
      font-weight: 700;
      font-size: 24px;
      line-height: 167%;
      text-align: center;
      color: #f8f200;
      background-color: var(--black-color);
      transition: color .5s ease-in, background .5s ease-in;

      &:hover {
        color: var(--black-color);
        background-color: var(--accent-color);
      }
    }
  }
}
